.button {
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #FFFFFF;
    min-width: 200px;
    padding: 0 20px;
    min-height: 46px;
    background: transparent;
    border-radius: 8px;
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    color: #FFFFFF;
    transition: 0.6s ease;
    &:hover {
        background: #fff;
        transition: 0.6s ease;
        color: #2B0955;
    }
}