.button {
    display: flex;
    align-items: center;
    justify-content: center;

    min-width: 190px;
    padding: 0 20px;
    min-height: 46px;
    background: #FBC508;
    border-radius: 8px;
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    color: #2B0955;
    transition: 0.6s ease;
    border: 1px solid #FBC508;
    &:hover {
        transition: 0.6s ease;
        background: transparent;
        color: #FBC508;
    }
}